import React from 'react'
import { Helmet } from 'react-helmet'
import Layout from '../components/layout'
import Elements from '../pages/elements'

class Home extends React.Component {
  render() {
    return (
      <Layout location="/">
        <Helmet
          htmlAttributes={{ lang: 'en' }}
          title="COVID-19 Case Evaluator"
          meta={[
            { name: 'description', content: 'A web application that evaluates the worse case and best case impact of COVID-19 on regions.' },
            { name: 'keywords', content: 'corona-virus, covid-19, covid19' },
          ]}
        >
        </Helmet>
        <Elements/>
      </Layout>
    )
  }
}

export default Home
